body, html {
    margin: 0;
    padding: 0;
}

#app {
    font-family: 'Caveat Brush', cursive;
    display: flex;
    align-items: center;
}

#block {
    flex: 1;
    margin: 5%;
}

svg {
    filter: drop-shadow(10px 10px #0005);
}

#text {
    text-align: center;
    font-size: 32vw;
    color: #F9C81D;
    text-shadow: 10px 10px #0005;
    white-space: nowrap;
}